import React, { Component } from 'react';
import MuiThemeProvider from 'material-ui/styles/MuiThemeProvider';
import AppBar from 'material-ui/AppBar';
import logo from './logo.svg';
import './App.css';
import { IconButton, FontIcon, Drawer, FlatButton } from 'material-ui';

import theme from './theme';

class App extends Component {
  constructor(props) {
    super(props);
    this.state = { open: false };
  }

  toggleFilterDrawer = () => this.setState({ open: !this.state.open });

  render() {
    return (
      <MuiThemeProvider muiTheme={theme}>
        <div className="App">
          <AppBar
            title="Web Skills"
            iconElementLeft={
              <IconButton href="https://aciccarello.github.io/">
                <FontIcon className="material-icons">arrow_back</FontIcon>
              </IconButton>
            }
            iconElementRight={
              <IconButton onClick={this.toggleFilterDrawer}>
                <FontIcon className="material-icons">filter_list</FontIcon>
              </IconButton>
            }
          />
          <div>
            <header className="App-header">
              <img src={logo} className="App-logo" alt="logo" />
              <h1 className="App-title">Under development</h1>
            </header>
            <Drawer width={250} openSecondary={true} open={this.state.open}>
              <p>Filters coming soon</p>
              <FlatButton
                label="Close"
                onClick={this.toggleFilterDrawer}
                fullWidth={true}
              />
            </Drawer>
          </div>
        </div>
      </MuiThemeProvider>
    );
  }
}

export default App;



